	text-decoration: underline;
}

.formContent{
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
}
.Loginform label {
	padding: 0 0 2px 0;
} 
.textBoxx{
	text-indent: 1rem;
	background: transparent;
	border-radius: 4px;
	border: 1px solid rgb(217, 217, 217);
	height: 2.3rem;
	width: 88%;
}
.textBoxx::-webkit-input-placeholder{
	text-indent: 1rem;	
}
.textBoxx::-ms-input-placeholder{
	text-indent: 1rem;	
}
.textBoxx::placeholder{
	text-indent: 1rem;	
}
.textBoxx:focus{
		box-shadow: 0 0 10px 3px rgb(53, 149, 255);
		background: white;

}
.btn-primary{
	width: 60%;
	color:white;
	border-style: none;
	height: 3rem;
	background: rgb(13, 128, 255);
	border-radius: 4px;
}
.btn-primary:hover{
	cursor: pointer;
	-webkit-transition: all .5s;
	transition: all .5s;
	background: rgb(0, 105, 217);
}
.btn-primary:active{
	box-shadow: 0 0 1px 5px rgb(53, 149, 255);
	-webkit-transition:  -webkit-transform .12s;
	transition:  -webkit-transform .12s;
	transition:  transform .12s;
	transition:  transform .12s, -webkit-transform .12s;
	-webkit-transform: scale(.93);
	        transform: scale(.93);
}
